import React, { useState } from "react";

import Container from "../atoms/Container";
import Text from "../atoms/Text";
import Button from "../atoms/Button";
import Input from "../atoms/Input";

import Modal from "../molecules/Modal";

const PinModal = ({
  title,
  message,
  action: cb,
  open,
  dismiss,
  preventDismiss,
}) => {
  const [pin, setPin] = useState("")
  const [error, setError] = useState("")

  const action = () => {
    setError("")
    if (!pin) return setError("Please enter PIN")

    cb(pin);
    dismiss();
  };

  return (
    <Modal open={open} dismiss={dismiss} preventDismiss={preventDismiss}>
      <Container
        flexCol="center"
        maxW="480px"
        w="90vw"
        h="auto"
        bg="bgContrast"
        p="16px"
        radius="12px"
      >
        <Text font="14px" p="0 0 12px" align="center" bold>
          {title}
        </Text>
        <Input
          radius="8px"
          placeholder="Withdrawal Pin"
          onChange={e => setPin(e.target.value)}
        />
        <Text font="12px" p="12px 0" align="center" multiline>
          {message}
        </Text>
        {error && (
          <Text font="12px" p="12px 0" align="center" text="danger" multiline>
            {error}
          </Text>
          )}
        <Container m="12px 0" display="grid" flow="column" gap="12px" wide>
          <Button
            bg="secondary"
            // color="black"
            p="16px"
            radius="24px"
            bold
            full
            onClick={dismiss}
          >
            Cancel
          </Button>
          <Button bg="primary" p="16px" radius="24px" bold full onClick={action}>
            Continue
          </Button>
        </Container>
      </Container>
    </Modal>
  );
};

export default PinModal;
